import React, { useState , useEffect } from "react";
import {FloatingLabel,Form} from 'react-bootstrap';
import {BrowserRouter as Router,Route} from 'react-router-dom';
import BasicSearch from "./Tabla";
import Swal from 'sweetalert2';
import Axios from 'axios';



function Phone() {

    const [nombreProducto,setnombreProducto]= useState('')
    const [Almacenamiento,setAlmacenamiento]= useState('')
    const [Ram,setRam]= useState('')
    const [Camara,setCamara]= useState('')
    const [Estado,setEstado]= useState([])
    const [EstadoSelect,setEstadoSelect]= useState([])
    const [Descripcion,setDescripcion]= useState('')

    useEffect(() => {
       
        setEstado(['Disponible','Agotado'])
        setEstadoSelect(['Seleccionar'])
        

    },[])

    const guardar = async(e)=>{
        e.preventDefault()
        const phones = {
            nombreProducto,
            Almacenamiento,
            Ram,
            Camara,
            Estado : EstadoSelect,
            Descripcion

        }

        if(nombreProducto === ""){

            Swal.fire({
                icon:'error',
                title:'Campo Obligatorio',
                showConfirmButton:false,
                timer: 1500
            })
        }
        
        else if(Estado === ""){

            Swal.fire({
                icon:'error',
                title:'Campo Obligatorio',
                showConfirmButton:false,
                timer: 1500
            })
        }
        
        else{
            
            const respuesta = await Axios.post('/Phones/crear',phones)
            const mensaje = respuesta.data.mensaje
            console.log(mensaje)

            Swal.fire({
                icon: 'success',
                title: mensaje,
                showConfirmButton:false,
                timer: 1500
            })
            

            e.target.reset();
            setnombreProducto("");
            setAlmacenamiento("");
            setRam("");
            setCamara("");
         

        }
    }

  return (

    <body>

    <div className="container-fluid">
      <div className="row">

        <div className="col s3">
        
            <div className="card col-9" style={{borderRadius: "1rem", margin:"1rem"}}>
                <div className="card-header bg-dark text-white  text-center">
                    <h4>Registrar Producto </h4>
                </div>

                <div className="card-body">
                    <form onSubmit={guardar}>
                        <div className="row">

                            <div className="col-md-6" >
                                <label>Nombre Producto</label>
                                <input type="text" className="form-control d-grid required" onChange={(e)=>setnombreProducto(e.target.value)} />
                            </div>

                            <div className="col-md-6" >
                                <label>Almacenamiento</label>
                                <input type="text" className="form-control d-grid required" onChange={(e)=>setAlmacenamiento(e.target.value)} />
                            </div>

                            <div className="col-md-6" >
                                <label>Ram</label>
                                <input type="text" className="form-control d-grid required" onChange={(e)=>setRam(e.target.value)} />
                            </div>        
                            
                            <div className="col-md-6" >
                                <label>Camara</label>
                                <input type="text" className="form-control d-grid required" onChange={(e)=>setCamara(e.target.value)} />
                            </div>  

                            <div className="col-md-6" >
                                <label>Estado</label>
                                <select className="form-control " name="select" onChange={(e)=>setEstadoSelect(e.target.value)} >

                                <option selected="true" disabled="disabled">{EstadoSelect}</option>
                                
                                    {
                                        Estado.map(Estados => (
                                            
                                            <option key={Estados}>
                                                {Estados}

                                            </option>
                                        ))
                                    }
                          
                                </select>
                            </div>  
                            
                            <FloatingLabel controlId="floatingTextarea2" label="">
                            Descripcion
                            <Form.Control
                                as="textarea"
                                style={{ height: "100px", marginTop: "0rem", marginBottom:"10px"  }} onChange={(e)=>setDescripcion(e.target.value)}/>
                            </FloatingLabel>    

                            <center>
                            <button type="submit" className="btn btn-info">
                                <span className="fa fa-save"></span> Guardar
                            </button>
                            </center>
                        </div>
                    </form>
                </div>

            </div>
        </div>

            <div className="col s3" style={{ marginTop:"1rem", marginLeft:"-9rem"}}>
            
            
                <Router>

                    <BasicSearch/>

                </Router>

             
            </div>

     </div>
    </div>
    
    </body>

  );
}

export default Phone;
