import React, { useState, useEffect, map } from "react";
import MaterialTable from 'material-table';
import Swal from 'sweetalert2';
import Axios from 'axios';
import { Row, Modal, Button, FloatingLabel,Form } from "react-bootstrap";




function BasicSearch() {


    const [productos, setProductos] = useState([])
    const [idProducto, setIdProducto] = useState('')
    const [nombreProducto, setnombreProducto] = useState('')
    const [Almacenamiento, setAlmacenamiento] = useState('')
    const [Ram, setRam] = useState('')
    const [Camara, setCamara] = useState('')
    const [Estado, setEstado] = useState([])
    const [EstadoSelect, setEstadoSelect] = useState([])
    const [Descripcion, setDescripcion] = useState('')


    useEffect(() => {

        optenerProductos()

        setEstado(['Disponible','Agotado'])
        setEstadoSelect(['Seleccionar'])
        

    }, [])

    // CREAR

    const optenerProductos = async () => {

        const respuesta = await Axios.get('/Phones/listarPhones')

        console.log(respuesta)
        setProductos(respuesta.data)
    }

    // ACTUALIZAR


    const optenerProActualizar = async (idParametro) => {


        setShow(true)
        const id = idParametro
        const respuesta = await Axios.get('/Phones/listar/'+id);

        console.log(respuesta.data)

        setIdProducto(respuesta.data._id)
        setnombreProducto(respuesta.data.nombreProducto)
        setAlmacenamiento(respuesta.data.Almacenamiento)
        setRam(respuesta.data.Ram)
        setCamara(respuesta.data.Camara)
        setEstadoSelect(respuesta.data.Estado)
        setDescripcion(respuesta.data.Descripcion)

    }

    const actualizar = async(e)=>{

        e.preventDefault();
        const id = idProducto
        const producto = {
            nombreProducto,
            Almacenamiento,
            Ram,
            Camara,
            Estado : EstadoSelect,
            Descripcion

        }

        const respuesta = await Axios.put('/Phones/actualizar/'+id,producto)
        const mensaje = respuesta.data.mensaje
        optenerProActualizar()

        Swal.fire({
            icon:'success',
            title: mensaje,
            showConfirmButton: false,
            timer:1500
        })
        
        setShow(false)
    }



    const eliminar = async (id) =>{

        const respuesta = await Axios.delete('/Phones/eliminar/'+id)
        const mensaje= respuesta.data.mensaje
        Swal.fire({
            icon:'success',
            title: mensaje,
            showConfirmButton: false,
            timer: 1500
        })


    }



    // MAPEAR TODOS LOS CAMPOS REGISTRADOS


    const data = productos.map((producto) => ({
        _id:producto._id,
        nombreProducto: producto.nombreProducto,
        Almacenamiento: producto.Almacenamiento,
        Ram: producto.Ram,
        Camara: producto.Camara,
        Estado: producto.Estado
    }))

    // ACCIONES MODALES

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);



    return (

        <div>

            <MaterialTable
                title="Lista Productos"
                columns={[
                    { title: 'PRODUCTO', field: 'nombreProducto' },
                    { title: 'ALMACENAMIENTO', field: 'Almacenamiento' },
                    { title: 'RAM', field: 'Ram' },
                    { title: 'CAMARA', field: 'Camara' },
                    { title: 'ESTADO', field: 'Estado', type: 'text' },


                ]}
                data={data}
                options={{
                    
                    search: true,
                    actionsColumnIndex: -1,


                }}
                actions={[
                    {
                        icon: 'delete',
                        tooltip: 'Eliminar',
                        onClick:(event,rowData)=>eliminar(rowData._id)
                    },
                    {
                        icon: 'edit',
                        tooltip: 'Actualizar',
                        onClick:(event,rowData)=> optenerProActualizar(rowData._id)


                    }
                ]}
            />

            <Modal 
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered

            show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>EDITAR PRODUCTO</Modal.Title>
                </Modal.Header>
                <Modal.Body>

                    <div className="container-fluid">
                    <div className="row">

                    <div className="col s100">

                    <div className="card col-12" style={{borderRadius: "1rem", margin: ""}}>
                    <div className="card-header bg-dark text-white  text-center">
                    <h4>Editar Producto</h4>
                    </div>

                    <div className="card-body">
                    <form onSubmit={"guardar"}>
                    <div className="row">

                    <div className="col-md-6" >
                    <label>Nombre Producto</label>
                    <input value={nombreProducto} type ="text" className="form-control d-grid required" onChange={(e) => setnombreProducto(e.target.value)} />
                    </div>

                    <div className="col-md-6" >
                    <label>Almacenamiento</label>
                    <input  value={Almacenamiento} type ="text" className="form-control d-grid required" onChange={(e) => setAlmacenamiento(e.target.value)} />
                    </div>

                    <div className="col-md-6" >
                    <label>Ram</label>
                    <input  value={Ram} type ="text" className="form-control d-grid required" onChange={(e) => setRam(e.target.value)} />
                    </div>

                    <div className="col-md-6" >
                    <label>Camara</label>
                    <input value={Camara} type ="text" className="form-control d-grid required" onChange={(e) => setCamara(e.target.value)} />
                    </div>

                    <div className="col-md-6" >
                    <label>Estado</label>
                    <select value={Estado} className="form-control " name="select" onChange={(e) => setEstadoSelect(e.target.value)} >

                    <option selected="true" disabled="disabled">{EstadoSelect}</option>

                {
                    Estado.map(Estados => (

                    <option key={Estados}>
                {Estados}

                    </option>
                ))
                }

                    </select>
                    </div>

                    <FloatingLabel controlId="floatingTextarea2" label="">
                    Descripcion
                    <Form.Control
                    value={Descripcion}
                    as="textarea"
                    style={{ height: "100px", marginTop: "0rem", marginBottom: "10px"  }} onChange={(e) => setDescripcion(e.target.value)}/>
                    </FloatingLabel>
                    </div>
                    </form>
                    </div>

                    </div>
                    </div>
                    </div>
                    </div>

                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={actualizar}>
                        Save Changes
                    </Button>
                </Modal.Footer>
            </Modal>

        </div>
    )
}


export default BasicSearch;
